import * as React from "react";
import { ElasticContainer } from './ElasticContainer';

export interface ElasticCanvasProps {}
export interface ElasticCanvasState {}

export class ElasticCanvas extends React.Component<ElasticCanvasProps, ElasticCanvasState> {
  private canvas: HTMLCanvasElement;

  constructor(props: ElasticCanvasProps){
    super(props);
  }

  componentDidMount() {
    console.log(this.canvas);
  }

  render() {
    return (
      <ElasticContainer>
        {({ height, width}) => (
          <canvas
            ref={(ref) => this.canvas = ref}
            height={height}
            width={width}
          />
        )}
      </ElasticContainer>
    );
  }
}



